  width: 100%;
  position: relative;
  height: 100%;
}
.wk-pop-out-enter-active,
.wk-pop-out-leave-active,
.wk-pop-in-enter-active,
.wk-pop-in-leave-active {
  will-change: transform;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
}
.wk-pop-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.wk-pop-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.wk-pop-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.wk-pop-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
