.flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.flex-primary {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
}

.flex-center {
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.flex-initial {
  -webkit-box-align: stretch !important;
  -webkit-align-items: initial !important;
  align-items: initial !important;
}

.show-oneline {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.show-twoline {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}



